:root {
  /* Color Palette - Playful yet Trustworthy */
  --primary: #ff6b6b;
  /* Coral Red - Energetic, warm */
  --primary-hover: #ee5253;
  --secondary: #4ecdc4;
  /* Turquoise - Calm, friendly */
  --secondary-hover: #36b9b0;
  --accent: #ffd93d;
  /* Sunny Yellow - Happiness */
  --background: #fffdf5;
  /* Warm Cream - Easy on eyes */
  --surface: #ffffff;
  /* Pure White - Cards/Sections */
  --text-main: #2d3436;
  /* Dark Grey - Readable, softer than black */
  --text-muted: #636e72;
  /* Muted Grey */
  --border: #e0e0e0;

  /* Spacing */
  --radius: 12px;
  --shadow-sm: 0 1px 2px rgba(0, 0, 0, 0.05);
  --shadow-md: 0 4px 6px rgba(0, 0, 0, 0.05);
  --shadow-lg: 0 10px 15px rgba(0, 0, 0, 0.1);
  --shadow-hover: 0 10px 25px rgba(0, 0, 0, 0.15);

  /* Animations */
  --transition-fast: 0.2s ease;
  --transition-medium: 0.3s ease;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  background-color: var(--background);
  color: var(--text-main);
  font-family: var(--font-inter), sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--font-nunito), sans-serif;
  font-weight: 700;
  margin-bottom: 0.5em;
  line-height: 1.2;
}

a {
  color: inherit;
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
  font-family: inherit;
}

/* Utilities */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0.75rem 1.5rem;
  border-radius: 50px;
  /* Pill shape is friendly */
  font-weight: 600;
  transition: all var(--transition-fast);
  gap: 0.5rem;
}

.btn-primary {
  background-color: var(--primary);
  color: white;
  box-shadow: 0 4px 10px rgba(255, 107, 107, 0.3);
}

.btn-primary:hover {
  background-color: var(--primary-hover);
  transform: translateY(-2px);
  box-shadow: 0 6px 15px rgba(255, 107, 107, 0.4);
}

.btn-secondary {
  background-color: var(--secondary);
  color: white;
}

.btn-secondary:hover {
  background-color: var(--secondary-hover);
  transform: translateY(-2px);
}
